.card-body{
    margin-top: 10px;
}
.card-title{
    text-align: left;
}

.inner{
    overflow: hidden;
}

.inner img{
    transition: all 1.5s ease;
}

.inner:hover img{
    transform:  scale(1.5);
}


.item-body{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}


